import React from "react";
import "./Info.css";
import img from "../images/live_pic2.jpg";
import { Link } from "react-router-dom";

function Info() {
  return (
    <div className="info">
      <figure>
        <h2>Live Information</h2>
        <img src={img} alt="" loading="lazy" />
      </figure>

      <div className="info__container">
        <h3>ライブ開催決定！</h3>
        <ul>
          <li>日時：5月5日 19時~</li>
          <li>
            場所：
            <a href="https://cocozahikone.wixsite.com/cocoza" target="_blank">
              ライブハウスCOCOZA
            </a>
          </li>
          <li>料金：¥2,000 (¥1,500(チケット料金) + ¥500(ドリンク代))</li>
        </ul>
        <p>
          ＊お問い合わせは<Link to="/contact">こちら</Link>
        </p>
      </div>
    </div>
  );
}

export default Info;
