import React from "react";
import { Link } from "react-router-dom";
import "./News.css";

function News() {
  return (
    <div className="news">
      <h2>~News~</h2>
      <h3>1.ライブ情報！</h3>
      <p>
        5月5日にライブを行います！詳細は<Link to="/information">こちら</Link>
        をご覧ください。
      </p>
      <h3>2."AVALANCHEアカウント"ができました！</h3>
      <p>
        新しいサービスを開始しました！
        <br />
        詳しくは<Link to="/profile">こちら</Link>！
      </p>
    </div>
  );
}

export default News;
